body,
html {
    padding: 0px;
    margin: 0px;
}
body {
    width: 100%;
}
.activities-item {
    padding: 16px 0px;
    display: flex;
    align-items: center;
    position: relative;
}
.chevrnon-scroll {
    position: absolute;
    top: 98%;
    left: 50%;
    cursor: pointer;
    transform: translate(-50%, -95%);
    animation: up-down 0.8s ease forwards infinite;
}
@keyframes up-down {
    from {
        top: 97%;
    }
    to {
        top: 98%;
    }
}
.activities-container {
    display: grid;
    grid-template-columns: 80% 20%;
    height: 100%;
    grid-template-rows: auto 92%;
    grid-template-areas: 'header header' 'activities calendar';
}
.activities-header {
    grid-area: header;
}
.activities-list {
    grid-area: activities;
}
.activities-calendar {
    grid-area: calendar;
}
#root {
    width: 100%;
    height: 100%;
}
.button-icon-dark {
    color: white;
}
.section {
    width: 100%;
    height: 86vh;
}
.welcome-text {
    z-index: 1;
}
.welcome-text-description {
    width: 30%;
    z-index: 1;
}
.backdrop {
    position: absolute;
    width: 100%;
    height: 100%;
    background: black;
    opacity: 0.6;
}
.home {
    position: relative;
    height: 94.3vh;
    background-size: 100% 100%;
}
.experience {
    height: 98vh;
    position: relative;
}
@media only screen and (max-width: 768px) {
    .welcome-text-description {
        width: 80%;
    }
    .activities-header {
        text-align: center;
        margin-bottom: 16px;
    }
    .activities-list {
        height: 100vh;
        overflow-y: scroll;
    }
    .activities-calendar {
        margin-top: 16px;
    }
    .activities-container {
        display: flex;
        min-height: 100vh;
        flex-direction: column;
    }
    .activities-item {
        text-align: center;
    }
    .home {
        height: 80.2vh;
        background-size: 2000px 900px;
        background-position-x: -82%;
    }
}
